@import url('https://fonts.googleapis.com/css2?family=Praise&family=Roboto+Condensed:ital,wght@1,300&display=swap');

html, body {
    max-width: 100%;
    overflow-x: hidden;
    
}
.PrivacyText{
        margin-top: 1rem;
        font-size: large;
        font-size: 15pt;
        font-weight: bold;
        
}
a{
    color:    #ccff33

}
.WelcomeScreen {
    
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    /* background: #ffe6e6; */
    background-image: url("../public/background1.jpg");
    
       
    }
    .WelcomeMessage{
        color:  #ff0066;
        margin-top: 5rem;
        font-size: 3rem;
        font-style: italic;
        font-family: cursive;
       
    }
  
    .LogInScreen{
        margin-top: 5rem;
        color: #660066;
        font-style: italic;
        font-size: 2.5rem;
    }
    .login-button {
    color: #494949;
    text-transform: uppercase;
    text-decoration: none;
    background: #ffffff;
    border: 4px solid #494949;
    display: inline-block;
    transition: all 0.4s ease 0s;
    min-width: 10rem;
    margin: 1rem;
     }
   
    .google-btn {
    width: 210px;
    height: 55px;
    margin-top: 4rem;
    background-color: #4285f4;
    border-radius: 2px;
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
    
    }
    .google-btn:hover {
    box-shadow: 0 0 6px #4285f4;
    }
    .google-btn:active {
        background: #1669f2;
        }
        .google-icon-wrapper {
        position: absolute;
        margin-top: 1px;
        margin-left: 1px;
        width: 50px;
        height: 50px;
        border-radius: 2px;
        background-color: #fff;
        }
        .google-icon {
        position: absolute;
        margin-top: 11px;
        margin-left: -8px;
        width: 18px;
        height: 18px;
        }
        .btn-text {
        float: right;
        color: #fff;
        font-size: 16px;
        letter-spacing: 0.2px;
        font-family: "Roboto";
        background-color: #4285f4;
        width: 80%;
        height: 100%;
        text-align: center;
        padding-left: 10.5px;
        }
       